.contacts {
  background-color: #fff5f6;
  margin: 0;
  // margin-top: -60px;

  &__grid {
    display: grid;
    justify-content: center;
  }

  &__tab {
    margin: 0;
    padding: 0;
    background-color: white;
    border-radius: 24px;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    max-width: 220px;
    min-height: 427px;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  &__name {
    max-height: 16px;
    margin: 43px auto 0px 20px;
    border-radius: 4px;
    display: inline-block;
    font-family: $font-primary;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    align-items: center;
    text-align: center;

    vertical-align: middle;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: #ffffff;

    &--cafe {
      width: 80px;
      background-color: #f0d1a5;
    }
    &--foodtruck {
      width: 130px;
      background-color: #c2e297;
    }
  }

  &__city {
    font-family: $font-primary;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    letter-spacing: 0.04em;
    color: #000000;
    margin: 25px auto 0 20px;
  }

  &__text {
    font-family: $font-primary;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #907e82;
    margin: 10px 20px 50px 20px;
    position: relative;

    &::after {
      margin: 0;
      content: ' ';
      position: absolute;
      border: 1px solid #e1e1e1;
      width: 100%;
      height: 0;
      left: 0;
      top: 61px;
    }
  }
  &__days {
    margin: 0px auto 0 20px;
    font-family: $font-primary;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    color: #000000;
  }
  &__hours {
    margin: 1px auto 10px 20px;
    font-family: $font-primary;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    color: #ffa5ba;

    &--bottom {
      position: relative;
      &::after {
        margin: 0;
        content: '';
        position: absolute;
        border: 1px solid #e1e1e1;
        width: calc(100% - 20px);
        height: 0;
        left: 0;
        top: 47px;
      }
    }
  }

  &__link {
    text-decoration: none;
    font-family: DM Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #907e82;
    margin-left: 20px;
    margin-right: auto;
    &:not(:last-child) {
      display: block;
      margin-top: 50px;
    }
  }
  &__buttonList {
    display: table;
    padding: 0;
    margin: 54px auto 0 auto;
    list-style-type: none;
  }

  &__buttonItem {
    display: flex;
    margin: 0 auto;
    display: flex;
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
  &__button {
    width: 161px;
    height: 44px;
    border-radius: 22px;
    border: none;
    font-family: $font-primary;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 21px;

    &--loc {
      background: #d41342;
      color: white;
    }
    &--fran {
      color: #d41342;
      background-color: white;
    }
  }
}
